<style lang="scss" scoped>
.items {
  border: 2px solid lightgray !important;
  .cart-items {
    margin: 20px 0px 20px 0px;
    padding: 20px 15px 0px 15px;
    max-height: 500px;
    overflow-x: hidden; /* Hide horizontal scrollbar */
    overflow-y: scroll; /* Add vertical scrollbar */
    .cart-item {
      padding: 10px;
      background-color: lightgray;
      .cart-item-image {
        width: 85px;
        height: 85px;
        object-fit: cover;
        padding: 0px;
        margin: 0px 10px 0px 0px;
        background-color: white;
        margin: 0px 10px 0px 0px;
      }
      .cart-item-texts {
        padding-left: 10px;
        text-align: left;
        // background-color: red;
        .item-name {
          font-size: 20px;
        }
        p {
          margin-bottom: 0px;
        }
        .qty-options {
          color: #339f25;
          padding: 0px;
          font-size: 20px;
          .plusminus {
            background: transparent;
            border: 0px;
            color: gray;
            font-size: 20px;
          }
        }
      }

      .cart-item-close-container {
        width: 20%;
        padding: 0px;
        position: relative;
        #x {
          position: absolute;
          top: 0px;
          right: 5px;
          background: transparent;
          border: 0px;
          color: gray;
        }
        #sub-total {
          //   background: white;
          position: absolute;
          bottom: 0px;
          right: 5px;
          font-size: 20px;
          .ws {
            font-size: 15px;
          }
        }
      }
    }
  }
